import '@material/web/textfield/filled-text-field';
import '@material/web/textfield/outlined-text-field';
import { Component, Event, Host, Prop, h, EventEmitter, Element } from '@stencil/core';
import { InputType } from './utils/enums.utils';

@Component({
  tag: 'tp-input',
  styleUrl: 'tp-input.scss',
  shadow: true,
})
export class TpInput {
  @Element() el: HTMLTpInputElement;

  @Prop({ reflect: true })
  label: string;

  @Prop({ reflect: true })
  type: InputType = InputType.Text;

  @Prop({ reflect: true })
  placeholder: string;

  @Prop({ reflect: true })
  outlined: boolean;

  @Prop({ reflect: false })
  startValue: any;

  @Event() didLoad: EventEmitter<{ ref: HTMLTpInputElement }>;

  @Event() userInput: EventEmitter<{ ref: HTMLTpInputElement; event: InputEvent }>;

  componentDidLoad() {
    this.didLoad.emit({ ref: this.el });
  }

  handleInput(event: InputEvent) {
    this.userInput.emit({ ref: this.el, event });
  }

  render() {
    const { label, placeholder, type, outlined, startValue } = this;

    return (
      <Host>
        {outlined ? (
          <md-outlined-text-field onInput={this.handleInput.bind(this)} label={label} type={type} placeholder={placeholder} value={startValue}></md-outlined-text-field>
        ) : (
          <md-filled-text-field label={label} type={type} placeholder={placeholder} value={startValue}></md-filled-text-field>
        )}
      </Host>
    );
  }
}
